import Vue from 'vue';
import Vuex from 'vuex';
Vue.use(Vuex);
// 创建一个对象来保存应用启动时的初始状态
// 需要维护的状态
const store = new Vuex.Store({
  // 放置初始状态 app启动的时候的全局的初始值
  state: { 
    supManTab: 0,//返回list页显示 待办/已办的值
    isEmable:false,//是否启用待办/已办的值,
    imgLen:[]
  },
    mutations: {
        setSupManTab(state,tab){
          state.isEmable=true
          state.supManTab=tab
        },
        getSupManTab(state){
          state.isEmable=false
        },//判断是不是返回 
        getImgLen(state,data,dataMonted){
          data&&data.length&& state.imgLen.push(...data)
          if(dataMonted&&dataMonted.length){
            state.imgLen=dataMonted
          }
        }
    },
    actions: {

    },

})
// 整合初始状态和变更函数，我们就得到了我们所需的 store
// 至此，这个 store 就可以连接到我们的应用中
export default store;
