import axios from "axios"
import qs from 'qs';
import global_config from "./globalConfig"
var setupWebViewJavascriptBridge = function(callback) {
    if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
      if (window.WebViewJavascriptBridge) {
        return callback(WebViewJavascriptBridge);
      }
      if (window.WVJBCallbacks) {
        return window.WVJBCallbacks.push(callback);
      }
      window.WVJBCallbacks = [callback];
      var WVJBIframe = document.createElement("iframe");
      WVJBIframe.style.display = "none";
      WVJBIframe.src = "wvjbscheme://__BRIDGE_LOADED__";
      document.documentElement.appendChild(WVJBIframe);
      setTimeout(function() {
        document.documentElement.removeChild(WVJBIframe);
      }, 0);
    } else{
      // 这是Android
      if (window.WebViewJavascriptBridge) {
        callback(WebViewJavascriptBridge);
      } else {
        document.addEventListener(
          "WebViewJavascriptBridgeReady",
          function() {
            callback(WebViewJavascriptBridge);
          },
          false
        );
      }
    } 
};
var isBridgeReady = (callback)=>{
  if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)){
      if(window.setupWebViewJavascriptBridge){
          callback();
      }else{
          document.addEventListener("jsBridgeReady",function(){
              callback();
          })
      }
  }else{
      if(window.setupWebViewJavascriptBridge){
        setTimeout(() => {
            callback();
        }, 0,false);     
      }else{
          document.addEventListener("WebViewJavascriptBridgeReady",function(){
              setTimeout(() => {
                  callback();
              }, 0,false);
          })
      }  
  }
}
//调用手机原生功能
var callNative = function(type, json, callback) {
	setupWebViewJavascriptBridge(function(bridge) {
	  bridge.callHandler(type, json, function(responseData) {
        if(typeof responseData == "string"){
            responseData = JSON.parse(responseData);
            if(responseData.result && typeof responseData.result == "string"){
                responseData.result = JSON.parse(responseData.result);
            }
		}else{
            if(responseData.result && typeof responseData.result == "string"){
                responseData.result = JSON.parse(responseData.result);
            }
        }
		callback(responseData.result);
	  });
	});
};
const callServer = (ev)=>{
    return new Promise((resolve,reject)=>{
        let type = global_config.type;
        switch(type)
        {
            case "online":  //在线请求方式
            axios.post("/umserver/core",deal_params(ev), {headers: {'Content-Type': 'application/x-www-form-urlencoded'}})
            .then(function(res){
                if(!res){
                    reject("服务器链接异常(h5)")
                }
                if(res.data.data.resultctx==""){
                    reject("服务连接异常(h5)")
                }
                if(res.data.data.resultctx.success=="true"||res.data.data.resultctx.success=="success"){
                  resolve(res.data.data.resultctx)
                }else{
                    // reject(res.data.data.resultctx)
                    resolve(res.data.data.resultctx)
                }
            },function(res){
                reject("服务器链接异常(h5)")
            }) 
            break;
            case "offline": //离线请求方式
            let callma = (()=>{    
                ev.params.headerparam=JSON.stringify({"Cookie":localStorage.getItem("token")});
                ev.params.cookieparam=JSON.stringify({"JSESSIONID":""}); 
                let data= {
                    "params" : {
                        "transtype":"call_ma",
                        "appid":global_config.appid,
                        "controller":global_config.controllerId,
                        "params":ev.params           
                    }
                };
                // console.log(data)
                console.log("参数");
                callNative("gotoNative", data, function(res){
                    if(typeof res == "string"){
                        res = JSON.parse(res);
                    }
                    // console.log("get ma result",res)
                    resolve(res);
                });
            })()
            isBridgeReady(callma);
            break;
            default:
            console.error("配置错误:globalConfig.js中配置type值可能配置错误") 
        } 
    })
}

axios.interceptors.request.use(function(request){ //request拦截器
    request.headers["mobileToken"] = ""
    
    return request;
},function(error){
    
});

axios.interceptors.response.use(function(response){ //response拦截器
    response.headers["Access-Control-Allow-Origin"] = "*";
    response.headers["Access-Control-Allow-Method"] = "POST,GET"
    return response;
},function(error){
    
});
