import Vue from 'vue';
import App from './App.vue';
import router from './router'; 
import axios from 'axios';
import store from './store';

// import VConsole from 'vconsole/dist/vconsole.min.js'
// var vConsole = new VConsole();
Vue.prototype.$http = axios;
import {callServer,callNative} from "./api/jsBridge";
Vue.prototype.callServer = callServer;
Vue.prototype.callNative = callNative;
import {NavBar,Tab,Tabs,Search,List,SwipeCell,Cell,CellGroup,Field,Button,Image,ImagePreview,Popup,Toast,Icon,Dialog} from 'vant';
Vue.use(NavBar).use(Tab).use(Tabs).use(Search).use(List).use(SwipeCell).use(Cell).use(CellGroup).use(Field).use(Button).use(Image).use(ImagePreview).use(Popup).use(Toast).use(Icon).use(Dialog);

// import Vant from 'vant';
// import 'vant/lib/index.css';
// Vue.use(Vant);
Vue.config.productionTip = false;

new Vue({
  render: h => h(App),
  router,
  store,
}).$mount('#app')
